import { Component, Input, OnInit, ChangeDetectorRef } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { GroupNode } from './group-node';
import { Subject } from 'rxjs';
import { takeUntil, filter } from 'rxjs/operators';
import { AssetSelectorComponent } from './asset-selector.component';
import { IIdentified, IManagedObject } from '@c8y/client';
import { Action } from '../action.enum';

@Component({
  selector: 'c8y-asset-selector-node',
  templateUrl: './asset-selector-node.component.html'
})
export class AssetSelectorNodeComponent implements OnInit {
  /**
   * The current node.
   */
  @Input() node: GroupNode;
  /**
   * All preselected items.
   */
  @Input() preselected: IIdentified | IIdentified[] = [];
  /**
   * Should the path be shown.
   */
  @Input() showPath: boolean = false;
  /**
   * Can the user select multiple assets.
   */
  @Input() multi: boolean = false;
  /**
   * The current path to the node.
   */
  @Input() view: 'tree' | 'miller' = 'tree';
  /**
   * Used only for miller-view, displays the column level for the current node.
   * E.g if the index is one, this will be second column.
   */
  @Input() index;
  /**
   * Sets the active node.
   */
  @Input() active: GroupNode;
  /**
   * All currently selected assets.
   */
  @Input() selectedItems: IIdentified[] | IIdentified = [];

  breadcrumb: string;
  /**
   * @ignore
   */
  level: number = 0;
  /**
   * @ignore
   */
  unsubscribe$ = new Subject<any>();

  /** sets the `btn-pending` class in the load more button */
  isLoading: boolean = false;
  /**
   * @ignore
   */
  get expandTitle() {
    return !this.node.open ? gettext('Expand') : gettext('Collapse');
  }

  /**
   * @ignore only di
   */
  constructor(
    private translateService: TranslateService,
    private cd: ChangeDetectorRef,
    public parentAssetSelector: AssetSelectorComponent
  ) {}

  /**
   * @ignore
   */
  async ngOnInit() {
    this.breadcrumb = this.node.label;
    this.setupBreadcrumbsAndLevel(this.node);

    if (this.node instanceof GroupNode) {
      this.node.hookEvents();
    }

    // open on startup
    if (this.node.root) {
      this.click();
    }

    // used for loading and to trigger change detection when the node is no longer loading.
    if (this.node.events) {
      this.node.events
        .pipe(
          takeUntil(this.unsubscribe$),
          filter((a: Action) => a === Action.LOADING_DONE)
        )
        .subscribe(() => {
          this.parentAssetSelector.onLoad.emit({
            loading: false,
            filterText: this.parentAssetSelector.filterText
          });
          this.cd.markForCheck();
        });
    }
  }

  /**
   * Opens a node.
   */
  click() {
    this.node.open = !this.node.open;
    this.node.click({ open: this.node.open });
  }

  setupBreadcrumbsAndLevel(node: GroupNode) {
    if (node.parents && node.parents.length) {
      const parent = node.parents[0] as GroupNode;
      this.breadcrumb =
        this.translateService.instant(parent.label) +
        ' > ' +
        this.translateService.instant(this.breadcrumb);
      this.level++;

      this.setupBreadcrumbsAndLevel(parent);
    }
  }

  /**
   * Selects the node and emits a change on the parent component.
   * @param node The node to select.
   */
  selected(node: GroupNode) {
    if (node.mo) {
      this.updateSelection(node.mo);
      return;
    }
    this.click();
  }

  /**
   * Handles clicks on a item in Miller View.
   * @param node The node that was clicked.
   */
  millerViewClick(node: GroupNode) {
    node.breadcrumb = this.breadcrumb;
    const shouldHandleDefault = this.parentAssetSelector.handleNextMillerViewColumn(
      node,
      this.index
    );
    if (shouldHandleDefault) {
      this.selected(node);
    }
  }

  /**
   * @ignore
   */
  ngOnDestroy(): void {
    this.unsubscribe$.next(true);
    this.unsubscribe$.complete();
  }

  isSelected() {
    if (!this.node.mo) {
      return false;
    }
    return this.parentAssetSelector.getIndexOfSelected(this.preselected, this.node.mo) > -1;
  }

  isActive() {
    if (this.active && this.node.mo) {
      return this.active.mo?.id === this.node.mo.id;
    }
    return false;
  }

  isGroupSelected() {
    if (this.selectedItems.some(selectedNode => selectedNode.id === this.node.mo?.id)) {
      return true;
    }
    return false;
  }

  private updateSelection(selectedMo: IManagedObject) {
    if (!this.multi) {
      this.parentAssetSelector.deselectAll(selectedMo);
      return;
    }
    if (this.isSelected()) {
      this.parentAssetSelector.deselect(selectedMo);
      return;
    }
    this.parentAssetSelector.select(selectedMo);
  }
}
