import { Injectable } from '@angular/core';
import { IIdentified, IManagedObject } from '@c8y/client';
import { AssetNodeService } from '../asset-node.service';
import { isArray, isNumber, isObject, isString } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class AssetSelectorService extends AssetNodeService {
  /**
   * Simplifies the object model based on the selected mode.
   * @param obj The selected asset.
   * @param mode The mode which will decide what type of model will be returned.
   */
  normalizeValue(
    obj: Partial<IManagedObject>,
    modelMode: 'simple' | 'full'
  ): Partial<IManagedObject> | IIdentified {
    return this.simplifyModel(this.normalizeModelValue(obj), modelMode);
  }

  simplifyModel(
    model: Partial<IManagedObject> | IIdentified[],
    mode: 'simple' | 'full'
  ): Partial<IManagedObject> | IIdentified {
    const mapModel = modelObj => {
      const { id, name, c8y_DeviceQueryString } = modelObj as IIdentified;
      return { id, name, ...(c8y_DeviceQueryString ? { c8y_DeviceQueryString } : {}) };
    };

    if (mode === 'full') {
      return model;
    }
    if (!isArray(model)) {
      return mapModel(model);
    }
    return model.map(mapModel);
  }

  private normalizeModelValue(value: any): IIdentified[] {
    if (isNumber(value) || isString(value)) {
      return [{ id: value }];
    }
    if (isArray(value)) {
      return value;
    }
    if (isObject(value)) {
      return [value];
    }
    return [];
  }
}
