import { NavigatorNode, gettext } from '@c8y/ngx-components';

export class LoadMoreNode extends NavigatorNode {
  static NAME = 'LoadMoreNode';
  label = gettext('Load more');
  icon = 'plus';
  droppable = true;

  constructor() {
    super();
    this.priority = -Infinity;
  }

  toString() {
    return LoadMoreNode.NAME;
  }

  isGroup() {
    return false;
  }
}
