import { ClickOptions, gettext } from '@c8y/ngx-components';
import { Action } from '../action.enum';
import { GroupNodeConfig } from './asset-selector.model';
import { GroupNode } from './group-node';
import { GroupNodeService } from './group-node.service';

export class UnassignedDevicesNode extends GroupNode {
  static NAME = 'UnassignedDevicesNode';
  label = gettext('Unassigned devices');
  icon = 'delete-folder';
  groupsSelectable = false;
  groupsOnly = false;
  showChildDevices = false;
  mo = {};

  constructor(protected service: GroupNodeService, config: GroupNodeConfig = {}) {
    super(service, config);
    this.priority = Infinity;
    this.showChildDevices = config.showChildDevices || false;
  }

  click(options: ClickOptions = {}) {
    this.hookEvents();
    if (options.open) {
      this.events.next(Action.FETCH);
    }
  }

  addManagedObject(mo) {
    this.add(this.service.createChildNode({ mo, showChildDevices: this.showChildDevices }));
  }

  fetch() {
    return this.service.getUnassignedDevices(this.showChildDevices, this.filterQuery$.value);
  }

  isGroup() {
    return true;
  }

  toString() {
    return UnassignedDevicesNode.NAME;
  }
}
